@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
.header {
    background: #FFFFFF 0% 0% no-repeat padding-box;
}

.header__lagreWrapper {
    box-shadow: 0px 3px 12px #00000012;
}

.header__wrapper {
    padding-top: 29px;
}

.header__controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
}

.header__search {
    flex: 0 0 21%;
    position: relative;
}

.header__search input {
    width: 100%;
    background: #F3F3F3;
    border: none;
    border-radius: 23px;
    font-size: 1.06rem;
    line-height: 1.5rem;
    font-weight: 300;
    padding: 11px 23px;
    color: #1D1D1D;
}

.search__icon {
    width: 6.8%;
    position: absolute;
    top: 10.75px;
    right: 23px;
}

.header__logo p {
    color: #D71117;
    font-size: 2rem;
    line-height: 2.9rem;
    font-weight: 500;
}

.header__control {
    display: flex;
    align-items: center;
}

.header__control>div {
    cursor: pointer;
}

.control__icon {
    fill: #1d1d1d;
    width: 28px;
    margin-left: 30px;
}

.header__cart {
    position: relative;
}

.header__cart .control__icon {
    margin-left: 0;
}

.header__cartQuantity {
    position: absolute;
    top: 24.5px;
    right: -7.5px;
    width: 15px;
    height: 15px;
    background-color: #D71117;
    color: #FFFFFF;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__cartQuantity p {
    font-size: 0.625rem;
    line-height: 0.94rem;
}

.header__loginPlace {
    display: flex;
    align-items: center;
    margin-left: 30px;
}

.header__loginPlace .control__icon {
    margin-right: 0;
    margin-left: 14px;
}

.header__loginPlace p {
    font-size: 1.06rem;
    line-height: 1.5rem;
    color: #1D1D1D;
}

.header__nav {
    padding: 0 18%;
    color: #1D1D1D;
    font-size: 1.25rem;
    line-height: 1.75rem;
    padding-bottom: 27px;
}

.header__nav ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__nav ul li p {
    cursor: pointer;
}

.bold {
    font-weight: 700;
}
.footer__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;
}

.footer__shares {
    display: flex;
    align-items: center;
}

.footer__info p {
    font-size: 1.125rem;
    line-height: 1.56rem;
    color: #1D1D1D;
}

.footer__icon {
    width: 26px;
}

.footer__sharesLogo {
    padding-right: 22px;
}

.footer__shares p {
    font-size: 2.06rem;
    font-weight: 500;
    line-height: 2.94rem;
    color: #D71117;
}

.fb__icon {
    padding-right: 16px;
}

.payment__icon {
    width: 100px;
}

.footer__payment {
    display: flex;
    align-items: center;
}

.footer__payment img {
    width: 100%;
}

.paymentsIcon {
    width: 60px;
    padding-right: 30px;
}

.paymentsIcon:last-child {
    padding-right: 0;
}
.mainSlider {}

.mainSlider__wrapper {
    height: 746px;
    position: relative;
}

.mainSlider__img {
    height: 100%;
}

.mainSlider__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 5;
}

.mainSlider__dots {
    display: flex;
    justify-content: center;
    margin-top: 27px;
}

.mainSlider__dotsWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 104px;
    height: 24px;
}

.mainSlider__dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #FCEEEF;
    cursor: pointer;
}

.dot__active {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background-color: #D71117;
}

.mainSlider__arrow .arrow__icon {
    position: absolute;
    z-index: 10;
    top: 355.5px;
    height: 35px;
    fill: #373737;
}

.mainSlider__arrow .arrow__icon:hover {
    cursor: pointer;
    fill: #D71117;
    box-shadow: 3px 3px 20px #00000014;
}

.mainSlider__left .arrow__icon {
    left: 20px;
}

.mainSlider__right .arrow__icon {
    right: 20px;
}

.animation {
    -webkit-animation: scale 0.5s;
            animation: scale 0.5s;
}

@-webkit-keyframes scale {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes scale {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
.stories__wrapper {
    height: 293px;
}

.stories .owl-carousel {
    height: 100%;
    position: relative;
}

.stories .owl-stage-outer {
    height: 100%;
    overflow: hidden;
}

.stories .owl-stage {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}

.stories .owl-item {
    height: 100%;
}

.storiesItem {
    height: 100%;
    border: 0.5px solid #fff;
    box-sizing: border-box;
}

.storiesItem__borderRed {
    height: 100%;
    border: 2px solid #D71117;
    border-radius: 15px;
    box-sizing: border-box;
}

.storiesItem__borderWhite {
    height: 100%;
    border: 3px solid #fff;
    border-radius: 10px;
    box-sizing: border-box;
    overflow: hidden;
}

.storiesItem__img {
    height: 100%;
    position: relative;
}

.storiesItem__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.stories .owl-prev,
.stories .owl-next {
    position: absolute;
    top: 0;
}

.stories .owl-prev {
    left: 0;
}

.stories .owl-next {
    right: 0;
}

.stories .owl-prev::after {
    content: '';
    position: absolute;
    top: 126.5px;
    left: 6px;
    width: 40px;
    height: 40px;
    background-color: #fff;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: 15px;
    background-repeat: no-repeat;
    cursor: pointer;
}

.stories .owl-next::after {
    content: '';
    position: absolute;
    top: 126.5px;
    right: 6px;
    width: 40px;
    height: 40px;
    background-color: #fff;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: 15px;
    background-repeat: no-repeat;
    cursor: pointer;
}
